<template>
  <div class="fashion">
  	<div class="fashion-div1">
  		<h2 class="fashion-div1-h2">你穿的这么帅，总是让人忍不住多看两眼！</h2>
  	</div>
  	<div v-for="obj in fashion1">
  		<img :data-src="obj.imgUrl"/>
  	</div>
  	<cationfixed></cationfixed>
  </div>
</template>

<script lang="babel">
import cationfixed from "./common/cationfixed";
export default {
	data(){//数据
		return{
			fashion1:[]
		}	
	},
	methods:{//js
		
	},
	components:{//注册模块
		cationfixed
	},
	mounted(){//得到数据的ajax
		console.log(1)
			var _this=this;
			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/fashion3.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.fashion1 = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' >
		.fashion{
			width: 20em;
			overflow-y: scroll;
			margin: 0 auto;
			background: #fff;
		}
		.fashion-div1{
			padding: 0.5em;
			
		}
		.fashion-div1-h2{
		font-weight: 600;
    font-size: 1.6em;
    margin: 1.5em 0 .4em;
    font-family: proxima-nova, sans-serif;
    line-height: 1.2;
    }
</style>


