export default{
	imgtop:null,
	mun:0,
	imgmun:0,
	fontFun:function(){//优化页面
		var _this=this;
		var imgs = $('img');
		var Height=$(document).height();
		var doHeight=$(window).clientHeight;
		var Top=$(window).scrollTop();
		var imgNewNode1=document.createElement("img");//创建的图片节点（内存中）
			for(var i=0;i<imgs.length;i++){
				_this.imgtop=imgs[i].offsetTop;
					if(imgs[i].offsetTop<Top+$(window).height()*2){
						
						var data=$("img").eq(i).attr("data-src");//实际需要加载图额地址						
						$("img").eq(i).fadeIn(100);
						if(imgNewNode1.complete){//检测图片是否加载完毕；会检测缓存
							$("img").eq(i).attr("src",data);
							_this.imgmun=i;
						}
						else{
							imgNewNode1.src=src;
							imgNewNode1.onload=function(){
								$("img").eq(i).src=src;
							}
						}
					}
			}	
	},
	init:function(){/*初始化*/
		var obj=this;
		obj.fontFun();
		$(window).scroll( function() {			
			obj.mun++;			
			if(obj.mun%130==0){
			obj.fontFun();//调用fontFun函数
			}
		} );
	}
};


