<template>
	<div class="classificationdiv">
		<headPlu></headPlu>
		<div style="overflow: hidden;height: 100%;position: relative;">
			<class1 :youthjson="url1" :youthjson2="url2"></class1>
		</div>
		<cationfixed></cationfixed>
	</div>
</template>

<script lang="babel">
	import headPlu from "../common/headPlu";
	import cationfixed from "../common/cationfixed";
	import class1 from "../common/youth";
	export default {

		data() { //数据
			return {
				url1:"./static/json/classification/classification1.json",
				url2:"./static/json/classification/classification2.json"
			}
		},
		methods: { //js
			
		},
		components: { //注册模块
			headPlu,
			cationfixed,
			class1
		},
		mounted() { //得到数据的ajax
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
</style>


