<template>
	<div>
		<headPlu></headPlu>
		<div style="overflow: hidden;height: 100%;position: relative;">
			<Date :youthjson="url1" :youthjson2="url2"></Date>
		</div>
		<cationfixed></cationfixed>
	</div>
</template>

<script lang="babel">
	import headPlu from "../common/headPlu";
	import cationfixed from "../common/cationfixed";
	import Date from "../common/youth";
	export default {

		data() { //数据
			return {
				url1:"./static/json/classification/date.json",
				url2:"./static/json/classification/classification1.json"
			}
		},
		methods: { //js
		},
		components: { //注册模块
			headPlu,
			cationfixed,
			Date
		},
		mounted() { //得到数据的ajax
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
	
</style>


