<template>
	<div>
		<headPlu></headPlu>
		<div style="overflow: hidden;height: 100%;position: relative;">
			<youth2 :youthjson="url1" :youthjson2="url2"></youth2>	
		</div>
		<cationfixed></cationfixed>
	</div>
</template>

<script lang="babel">
	import headPlu from "./common/headPlu";
	import cationfixed from "./common/cationfixed";
	import youth2 from "./common/youth";
	export default {

		data() { //数据
			return {
				url1:"./static/json/youth2.json",
				url2:"./static/json/youth.json"
			}
		},
		methods: { //js
			
		},
		components: { //注册模块
			headPlu,
			cationfixed,
			youth2
		},
		mounted() { //得到数据的ajax
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
	.youthdiv {
		width: 46.7%;
		margin: 0.1rem;
	}
</style>


