<template>
   <div class="swiper-container">
  	<div v-for="obj in vip" class="swiper-wrapper">
  		<img :data-src="obj.imgUrl" class="swiper-slide" src="//img.sanfu.com/img/index_boy/zwt.gif"/>
  	</div>
  </div>
</template>

<script lang="babel">
export default {
	props:{
		
	},
	data(){//数据
		return{
			vip:[]
		}	
	},
	methods:{//js

	},
	components:{//注册模块
	},
	beforeUpdate(){
	},
	updated(){
var swiper = new Swiper('.swiper-container', {
        pagination: '.swiper-pagination',
        direction: 'vertical',
        slidesPerView: 1,
        paginationClickable: true,
//      spaceBetween: 30,
        mousewheelControl: true
    });
	},
	beforeCreate(){

	},
	mounted(){//得到数据的ajax
var _this = this;

			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/vip.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.vip = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
			
			
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'scoped>
    .swiper-container {
        width: 100%;
        height: 100%;
        margin-left: auto;
        margin-right: auto;
    }
    .swiper-slide {
        text-align: center;
        font-size: 18px;
        background: #fff;

        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
    }
</style>


